<template>
    <div id="upload">
        <img src="../assets/picture.svg" />
        <div class="btn">
            <el-row>
                <el-button type="primary"  @click="clickButton">初审通过</el-button>
                <el-dialog :visible.sync="passDialogVisible" >
                    <div style="margin-bottom: 10px;">
                      <img src="../assets/warn.svg" alt="">
                        温馨提示
                    </div>
                    <span style="color: red; margin-left: 35px;">您是否确认通过该申请？</span>
                    <span slot="footer" class="dialog-footer">
                        <el-button @click="passDialogVisible = false">取 消</el-button>
                        <el-button type="primary" @click="passDialogVisible = false">确 定</el-button>
                    </span>
                </el-dialog>

                <el-button @click="clickButton">补充材料</el-button>
                <el-dialog :visible.sync="addDialogVisible" >
                    <div style="margin-bottom: 10px;">
                      <img src="../assets/warn.svg" alt="">
                        温馨提示
                    </div>
                    <div style="margin-left: 35px; margin-bottom: 10px">请告知申请人需要修改的材料内容：</div>
                    <textarea name="" id="" cols="30" rows="10" style="margin-left: 35px; width: 40vw"></textarea>
                    <span slot="footer" class="dialog-footer">
                        <el-button @click="addDialogVisible = false">取 消</el-button>
                        <el-button type="primary" @click="addDialogVisible = false">确 定</el-button>
                    </span>
                </el-dialog>

                <el-button type="danger" @click="clickButton">审核不通过</el-button>
                <el-dialog :visible.sync="failDialogVisible" >
                    <div style="margin-bottom: 10px;">
                      <img src="../assets/warn.svg" alt="">
                        温馨提示
                    </div>
                    <span style="margin-left: 35px;">审核不通过的申请单，申请人无法继续更改，是否确认？</span>
                    <span slot="footer" class="dialog-footer">
                        <el-button @click="failDialogVisible = false">取 消</el-button>
                        <el-button type="primary" @click="failDialogVisible = false">确 定</el-button>
                    </span>
                </el-dialog>
            </el-row>
        </div>
    </div>
</template>

<script>
import { mapState } from 'vuex';
  export default{
    data() {
      return {
        passDialogVisible: false,
        addDialogVisible: false,
        failDialogVisible: false,
      };
    },
    computed: {
        ...mapState([
            'examineeNumber',
            'studentName',
            'schoolName',
        ])
    },
    methods: {
      clickButton(event) {
        const button = (event.target.textContent);
        if((this.studentName && this.examineeNumber && this.schoolName) === ""){
            alert('请确认信息是否填充完');
        }else if(button === '初审通过'){
            this.passDialogVisible = true;
            this.printData();
        }else if(button === '补充材料'){
            this.addDialogVisible = true
            this.printData();

        }else{
            this.failDialogVisible = true
            this.printData();
        }
        
      },
      printData(){
        let array = [this.studentName, this.examineeNumber, this.schoolName];
            array.forEach((item)=>{
                if(item.length<=10){
                let reverseData = item.split('')
                console.log(reverseData.reverse());
            }
        })
      }
    }
  };
</script>

<style lang="scss">
    #upload{
        margin-left: 50px;
        display: flex;
        justify-content: space-between;
        .btn{
            margin-right: 250px;
            margin-top: 150px;
            .el-button{
                margin-left: 35px;
            }
            .el-dialog{
                .el-dialog__header{
                    display: none;
                }
                .el-dialog__body{
                    justify-items: center;
                    align-items: center;
                    padding-top: 10px;

                    img{
                        width: 25px;
                        height: 25px;
                        margin-right: 5px;
                    }
                }
            }
        }
    }
</style>