<template>
    <div id="nav">
        <div class="left">
            <img src="../assets/education.png"/>
            <div>某管理系统</div>   
            <router-link to="/">页面A</router-link> 
            <router-link to="/pageB">页面B</router-link>
        </div>
        <div class="right">
            <div class="search">
              <img src="../assets/search.svg" class="searchIcon"/>
              <input type="text" class="bar" placeholder="请输入内容">
            </div>
            <el-dropdown>
              <span class="el-dropdown-link">
                <img src="../assets/messages.svg" />
              </span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item>消息一</el-dropdown-item>
                <el-dropdown-item>消息二</el-dropdown-item>
                <el-dropdown-item>消息三</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
            <img :src="profilePicture" class="head" style="width=2em;height=2em;" @click.stop="uploadHeadImg"/>
            <input type="file" accept="image/*" @change="handleFile" class="hiddenInput"/>
            <div>{{name}}</div>
        </div>
    </div>
</template>

<script lang="ts">

  export default {
    name: 'Nav',
    data(){
        return{
            name: "zhang san",
            profilePicture: require("../assets/头像.svg")
        }
    },
    methods:{
      uploadHeadImg() {
        this.$el.querySelector('.hiddenInput').click()
      },
      handleFile(e) {
        let $target = e.target || e.srcElement
        let file = $target.files[0]
        var reader = new FileReader()
        reader.onload = (data) => {
          let res = data.target || data.target
          this.profilePicture = res.result
        }
        reader.readAsDataURL(file)
      },
    }
  };
</script>

<style lang="scss">
  @import "~@/assets/style/nav.scss";
</style>